import { toast } from "react-toastify";
const options = {
    position: "top-center",
    autoClose: 5000,
    hideProgressBar: false,
    closeOnClick: true,
    pauseOnHover: true,
    draggable: true,
    progress: undefined,
    theme: "colored",
}
export const successMsg = (msg)=>{
    toast.success(msg, options)
}
export const errorMsg = (msg)=>{
    toast.error(msg, options)
}