import { createSlice } from "@reduxjs/toolkit";

const spinnerSlice = createSlice({
    name: 'spinner',
    initialState: {
        value: false
    },
    reducers: {
        startSpinner: state => {
            state.value = true
        },
        stopSpinner: state => {
            state.value = false
        }
    }
})
export const { startSpinner, stopSpinner } = spinnerSlice.actions
export default spinnerSlice.reducer


